.ModeBtncontainer{
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.4s;
}
.switch{
    position: relative;
    display: inline-block;
    width: 100px;
    height: 30px;
}
.switch input{
    opacity: 0;
    width: 0;
    height: 0;
}
.slider{
    margin-top: -5px;
    height: 40px;
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ffecb3;
    transition: 0.4s;
}
.slider::before{
    position: absolute;
    content: "";
    height: 40px;
    width: 40px;
    transition: 0.4s;
}
input:checked + .slider{
    background-color: #323E52;
}
input:focus + .slider{
    box-shadow: 0 0 0 #2193F3;
}
input:checked + .slider:before{
    transform: translate(60px);
}
.slider.round{
    border-radius: 34px;
}

.slider.round:before{
    border-radius: 50%;
}
